<template>
	<div>
		<h3>Todos</h3>
		<div class="legend">
			<span>Double click to mark as completed</span>
			<span> <span class="incomplete-box"></span> = Incomplete </span>
			<span> <span class="complete-box"></span> = Complete </span>
		</div>
		<div class="todoS">
			<div
				@dblclick="onDblclick(todo)"
				v-for="todo in allTodoS"
				:key="todo.id"
				class="todo"
				v-bind:class="{ 'is-complete': todo.completed }"
			>
				{{ todo.title }}
				<i @click="deleteTodo(todo.id)" class="fas fa-trash-alt"></i>
			</div>
		</div>
	</div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";

export default {
	name: "TodoS",
	methods: {
		...mapActions(["fetchTodoS", "deleteTodo", "updateTodo"]),
		onDblclick(todo) {
			const updTodo = {
				id: todo.id,
				title: todo.title,
				completed: !todo.completed,
			};
			this.updateTodo(updTodo);
		},
	},
	computed: mapGetters(["allTodoS"]),
	created() {
		this.fetchTodoS();
	},
};
</script>

<style scoped>
.todoS {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	grid-gap: 1rem;
}

.todo {
	border: 1px solid #ccc;
	background: #41b883;
	padding: 1rem;
	border-radius: 5px;
	text-align: center;
	position: relative;
	cursor: pointer;
}
i {
	position: absolute;
	bottom: 10px;
	right: 10px;
	color: #fff;
	cursor: pointer;
}
.legend {
	display: flex;
	justify-content: space-around;
	margin-bottom: 1rem;
}

.complete-box {
	display: inline-block;
	width: 10px;
	height: 10px;
	background: #35495e;
}

.incomplete-box {
	display: inline-block;
	width: 10px;
	height: 10px;
	background: #41b883;
}

.is-complete {
	background: #35495e;
	color: #ffff;
}
@media (max-width: 500px) {
	.todoS {
		grid-template-columns: 1fr;
	}
}
</style>
